import React, { Component } from 'react';

import HeaderLogo from '../_Header/HeaderLogo.js';

export default class GameOver extends Component {
    render() {
        if (this.props.final_score === 30) {
            return(
                <div className="game-over">
                <h1 className="game-over-title">Поздравляем</h1>
                <p className="game-over-subtitle">Вы прошли викторину, набрав максимально количество баллов ({this.props.final_score})</p>

                <div className="game-over-congratulations">
                    <HeaderLogo/>
                    <p className="game-over-congratulations-text">
                        Сообщество SongAnimal искренне поздравляет вас с абсолютной победой
                        и считает вас одним из лучших знатоков природы и фауны нашего мира 
                    </p>
                </div>

                <button className="game-over-button" onClick={this.props.onClick}>
                    Попробовать еще раз!
                </button>
            </div>
            )
        }
        else {
            return (
                <div className="game-over">
                    <h1 className="game-over-title">Поздравляем</h1>
                    <p className="game-over-subtitle">Вы прошли викторину, набрав {this.props.final_score} из 30 возможных баллов</p>
    
                    <button className="game-over-button" onClick={this.props.onClick}>
                        Попробовать еще раз!
                    </button>
                </div>
            )
        }
    }
}
